.section {
  padding-top: 130px;
  padding-bottom: 130px;

  @include vp-767 {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  &__title-h2 {
    margin: 0;

    font-family: $ff-arbutus;
    font-size: 45px;
    font-weight: 400;
    line-height: 56px;
    color: $color-blue-whale;

    span {
      color: $color-mountain-meadow;
    }
  }

  &__title-h3 {
    margin: 0;

    font-family: $ff-arbutus;
    font-size: 35px;
    font-weight: 400;
    line-height: 44px;
    color: $color-blue-whale;

    span {
      color: $color-mountain-meadow;
    }
  }

  &__subtitle {
    margin: 0;

    font-family: $ff-montserrat;
    font-weight: 500;
  }

  &__subtitle-upper {
    margin: 0;

    font-family: $ff-montserrat;
    font-size: 14px;
    font-weight: 600;
    text-transform: uppercase;
    color: $color-slate-grey;
  }

  &__icons {
    display: inline-flex;
    align-items: center;
  }

  &__icons-dots {
    display: flex;

    margin-right: 30px;
    margin-left: 30px;

    span {
      width: 5px;
      height: 5px;
      border-radius: 50%;

      background-color: $color-mountain-meadow;
    }

    span + span {
      margin-left: 9px;
    }
  }

  .section__icons--vertical {
    flex-direction: column;

    &__icons-dots {
      flex-direction: column;

      margin-top: 30px;
      margin-left: 0;
      margin-right: 0;

      span + span {
        margin-left: 0;
        margin-top: 9px;
      }
    }
  }
}

.section--light-dark {
  padding-bottom: 0;

  @include vp-767 {
    padding-bottom: 0;
  }
}

.section--dark {
  background-color: $color-blue-whale-2;

  .section__title-h2,
  .section__title-h3,
  .section__subtitle {
    color: $color-default-white;
  }
}

