.section--intro {
  padding-top: 70px;

  @include vp-767 {
    padding-top: 150px;
  }
}

.section__icons + .section__title-h2 {
  margin-top: 30px;
}

.content + .section__title-h2 {
  margin-top: 80px;
}

.section__title-h2 + .content {
  margin-top: 80px;
}

.section__subtitle + .content {
  margin-top: 60px;
}

.section__title-h2 + .section__subtitle {
  margin-top: 25px;
}

.section__subtitle + .section__subtitle {
  margin-top: 28px;
}

.section__subtitle + .btn {
  margin-top: 40px;
}

.content + .btn {
  margin-top: 80px;
}
